$red: #F76D7C;
$pink: pink;
$blue: #0062FF;
$gray-f7: #f7f7f7;
$gray-8a: #8a8a8a;
$gray-51: #515151;
.search-nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
}

.search-bar {
    display: flex;
    align-items: center;
    height: 32px;
    border-radius: 16px;
    background-color: #f7f7f7;



    .image {
        margin-left: 16px;
        width: 20px;
        height: 20px;
    }

    .placeholder {
        margin-left: 10px;
        color: #bfbfbf;
    }

    .input {
        margin: 0 8px;
    }
}