.home-header {

    display: flex;
    justify-content: space-between;
    align-items: center;

    margin: 24px 16px;

    .title {
        font-size: 16px;
        font-weight: bold;
    }

    .right {

        display: flex;
        align-items: center;

        span {
            color: #bfbfbf;
        }

        img {
            width: 16px;
        }

    }

}